.cell-container {
    width: 80%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
}

.cell-svg {
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: visible;
}

.center-svg {
    width: 3vh;
    height: 3vh;
}

.in-last-move {
    border: 3px solid blue;
}