.piece {
    width: 100%;
    height: 100%;
    border-width: 3px;
    border-style: solid;
    flex-shrink: 0;
    box-shadow: 1.95px 1.95px 2.6px black;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    color: white;
    z-index: 1;
}

.orange {
    background-color: rgb(216,108,0);
    border-color: rgb(216,108,0);
}

.blue {
    background-color: rgb(40, 104, 160);
    border-color: rgb(40, 104, 160);
}

.green {
    background-color: rgb(112,156,0);
    border-color: rgb(112,156,0);
}

.purple {
    background-color: rgb(144,68,160);
    border-color: rgb(144,68,160);
}

.selected {
    border-color: red;
}

.en {
    font-size: 18px;
}
