.lobby-container {
    background-color: rgb(65,68,37);
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.button-container {
    width: 60%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.board-container {
    background-color: rgb(65,68,37);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.formation-container {
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;
    width: 30vh;
    height: 40vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.board {
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;
    width: 89vh;
    height: 89vh;
    position: relative;
}

.player-territory {
    display: grid;
    width: 25vh;
    height: 32vh;
    grid-template-columns: 5vh 5vh 5vh 5vh 5vh;
    grid-template-rows: 5.33vh 5.33vh 5.33vh 5.33vh 5.33vh 5.33vh;
    justify-items: center;
    align-items: center;
}

.top-territory, .bottom-territory {
    grid-column: 2;
}

.top-container, .bottom-container {
    display: grid;
    grid-template-columns: 32vh 25vh 32vh;
    justify-items: center; 
    width: 100%;
    height: 32vh;
}

.middle-container {
    display: grid;
    width: 100%;
    height: 25vh;
    grid-template-columns: 32vh 25vh 32vh;
    grid-template-rows: 25vh;
}

.left-territory {
    transform: rotate(90deg) translate(-3.5vh,-3.5vh);
    z-index: 1;
}

.right-territory {
    transform: rotate(-90deg) translate(3.5vh,3.5vh);
    z-index: 1;
}

.center-territory {
    display: grid;
    width: 25vh;
    height: 25vh;
    grid-template-columns: 5vh 5vh 5vh 5vh 5vh;
    grid-template-rows: 5vh 5vh 5vh 5vh 5vh;
    justify-items: center;
    align-items: center;
}